.sidebar {
  width:30vh;
  height: 150vh;
  background-color: #333;
  color: white;
}

.sidebar h2 {
  text-align: center;
}

.sidebar ul {
  list-style-type: none;
}

.sidebar ul li {
  padding: 15px;
}

.sidebar ul li a {
  color: white;
  text-decoration: none;
}

.sidebar ul li a:hover {
  text-decoration: underline;
}
